* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #e8e8e8;
}

#refresh {
  display: block;
  height: 2rem;
  text-align: center;
  font-size: 1.5rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  color:blue;
  text-decoration: underline;
  cursor: pointer;
}


@media only screen and (min-width: 512px) {
  html {
    font-size: 16px;
  }
}

@media only screen and (min-width: 720px) {
  html {
    font-size: 14px;
  }
}

@media only screen and (min-width: 980px) {
  html {
    font-size: 12px;
  }
}