#user-container {
    width: 320px;
    margin-left: calc((100% - 320px)/2);

    padding: 8px;
    background-color: rgb(211, 211, 211);
    margin-bottom: 0.5rem;
}

#user-container .heading {
    display: flex;
}

#user-container  .creatorContainer {
    display: block;
    width: 100%;
    padding-right: 1rem;
}

#user-container  .creator {
    display: flex;
    justify-content: space-between;
}

.user-options-container {
    margin-top: 10px;
}

.user-options-container div {
    text-align: center;
    font-size: 1rem;
    color: blue;
    cursor:pointer;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.user-options-container div:hover {
    background-color: rgb(142, 176, 187);
    border-radius: 0.5rem;
}
