.loading {
    color: red;
    align-self: center;
}

.threaditem {
    margin-top: 1rem;
    width: 90%;
    margin-left: 4%;
    padding: 0.5rem;
    background-color: rgb(211, 211, 211);
}

.thread-heading {
    display: flex;
}

.thread-heading img {
    margin-right: 1rem;
    padding: 0.2rem;
}

.thread-heading .creatorContainer {
    display: block;
    width: 100%;
    padding-right: 1rem;
}

.thread-heading .creator {
    display: flex;
    justify-content: space-between;
}

.creator-name {
    font-weight: bold;
    cursor:default;
}

.creator-name, .thread-title, .thread-date {
    font-size: 1rem;
}
.thread-date {
    font-weight: 300;
}

.thread-description {
    width: 90%;
    margin-left: 5%;
    font-size: 1rem;
    line-height: 24px;
}

.thread-options-container {
    align-self: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 5%;
    margin-top: 10px;
    width: 90%;
}

.thread-options-container div {
    font-size: 1rem;
    color: blue;
    cursor:pointer;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

.thread-options-container div:hover {
    background-color: rgb(142, 176, 187);
    border-radius: 0.5rem;
}

@media only screen and (min-width: 512px) {
    .threaditem {
        width: 80%;
        margin-left: 10%;
    }
  }
  
@media only screen and (min-width: 720px) {
    .threaditem  {
        width: 65%;
        margin-left: 17.5%;
    }
  }
  
@media only screen and (min-width: 980px) {
    .threaditem  {
        width: 50%;
        margin-left: 25%;
    }
}
