#dashboard {
    width: 300px;
    margin-left: calc((100% - 300px)/2);
    margin-top: 2rem;
}

#dashboard h3 {
    text-align: center;
}

#dashboard .statistics {
    display: grid;
    grid-template-columns: 1fr 2rem;
    grid-row-gap: 0.3rem;
    margin-top: 1rem;
}

#dashboard .statistics div {
    font-size: 1.2rem;
}
