#replies-container {
    width: 95%;
    margin-left: 1%;
    margin-top: 1rem;
    padding: 8px;
    background-color: rgb(230, 230, 230);
}

#replies-container .thread-description {
    width: 105% !important;
    margin-left: -0.5rem;
    /*border: 1px solid red;*/
}

#text-reply {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 1rem;
}

#text-reply input {
    width: 80%;
    height: 1.2rem;
    font-size: 1rem;
    padding: 5px;
}

#text-reply button {
    width: 15%;
    font-size: 1rem;
}
