header {
    background-color: rgba(115, 170, 220, 1);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0.5rem;
}
  
header h1 {
    font-size:  x-large;
    margin-left: 1rem;
    align-self: center;
    cursor: default;
}

header input {
    align-self: center;
    margin-right: 1rem;
    font-size: 1rem;
    height: 2rem;
    width: 10rem;
    border: none; 
    border:solid 1px rgb(64, 108, 150);
    border-radius: 10px;
    padding-left: 12px;
}

nav {
   display: flex;
   justify-content: center;
   margin-top: 1rem;
   margin-bottom: 1rem;
}

nav ul {
    display: flex;
    align-self: center;
}

nav ul li {
    font-size: 1.2rem;
    list-style-type: none;
    cursor: pointer;
    padding-right: 0.5rem;
    padding-left: 0.5rem;
}

nav ul li:hover {
    color: blue;
    text-decoration: underline;
}

nav ul span {
    cursor: default;
    padding-left: 0.3rem;
    padding-right: 0.3rem;
}

.active-nav {
    background-color: lightblue;
    border-radius: 0.5rem;
}
  
@media screen and (min-width: 600px) {
    header input {
        width: 12rem;
    }
}
