#form {
   width: 70%;
   margin-left: 15%;
   margin-top: 3rem;
   display: flex;
   flex-direction: column;
}

#form h2 {
    text-align: center;
    margin-bottom: 1rem;
}

#form label {
    text-align: center;
    margin-bottom: 0.2rem;
}

#form input {
    margin-bottom: 1rem;
    font-size: 1.1rem;
}

#form .submit {
    margin-top: 1rem;
}

@media only screen and (min-width: 768px) {
    #form {
        width: 50%;
        margin-left: 25%;
    }
}
